:root {
  --main-bg-color: #181818;
  --light-blue-color: rgb(0, 210, 255);
  --light-yellow-color: #ffff80;
  --green-color: springgreen;
  --pale-green-color: mediumspringgreen;
}

html {
  font-size: 16px;
}

body {
  margin: 0;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  box-sizing: border-box;
  background-color: #181818;
  background-color: var(--main-bg-color);
  height: 100%;  
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h3 {
  font-family: 'Montserrat', sans-serif;
  color: white;
  font-size: 1.1rem;
  font-weight: 600;
}

.fadeInAnimation {
  -webkit-animation: fadeIn ease-in 0.3s;
          animation: fadeIn ease-in 0.3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}


@media screen and (max-width: 1200px) { h2.page-heading { font-size: 1.6em; } }
