.form-container {
    width: 100%;
    --form-bg-color: #333;
    --form-input-bg-color: #555;
    --font-family: 'Open Sans', sans-serif;
    --form-text-color: white;
    --form-input-focus-bg-color: #777;
}

.form-container form {
    font-family: var(--font-family);
    font-size: 16px;
    width: 55%;
    max-width: 600px;
    margin: 0 auto;
    padding: 0.8em 1.75em 1.75em 1.75em;
    border-radius: 0.75em;
    background-color: var(--form-bg-color);
}

.form__input,
.form__label,
.form__textarea,
.form__button {
    display: block;
    width: 100%;
    padding: 0.4em;
    margin: 0.5em auto 0 auto;
    border-radius: 0.5em;
    font-family: var(--font-family);
    font-size: 1em;
    background-color: var(--form-input-bg-color);
    color: var(--form-text-color);
    outline: none;
    border: none;
    box-sizing: border-box;
}

input:-webkit-autofill,
input:-webkit-autofill:hover {
    border: 1px solid var(--form-bg-color);
    box-shadow: 0 0 0 30px var(--form-input-bg-color) inset !important;
    -webkit-box-shadow: 0 0 0 30px var(--form-input-bg-color) inset !important;
    -webkit-text-fill-color: white;
}

input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    border: 1px solid springgreen;
    box-shadow:  0 0 0 30px var(--form-input-bg-color) inset !important;
    -webkit-box-shadow: 0 0 0 30px var(--form-input-focus-bg-color) inset !important;
    -webkit-text-fill-color: white;
}

.form__input:focus,
.form__textarea:focus {
    background-color: var(--form-input-focus-bg-color);
    box-shadow: 0 0 2px 2px springgreen;   
}

.form__label {
    margin-top: 1.3em;
    padding: 0;
    background-color: inherit;
    color: var(--light-blue-color);
    font-size: 1.05em;
    font-weight: 700;
    letter-spacing: 0.5px;
}

.form__textarea-label {
    margin-top: 0.7em;
}

.form__textarea {
    height: 10em;
    margin-bottom: 0em;
}

.form__button {
    background-color: mediumvioletred;
    cursor: pointer;
    text-transform: uppercase;
    color: white;
    font-size: 1.05em;
    font-weight: 700;
    letter-spacing: 3px;
    padding: 0.2em 0.4em;
    transition: 0.2s;
}

.form__button:hover {
    background-color: rgb(221, 72, 122);
}

.form__button:active {
    background-color: rgb(209, 50, 58);
}

.form__input::-webkit-input-placeholder, .form__textarea::-webkit-input-placeholder {
    font-size: 0.8em;
    color: #aaa;
}

.form__input:-ms-input-placeholder, .form__textarea:-ms-input-placeholder {
    font-size: 0.8em;
    color: #aaa;
}

.form__input::placeholder,
.form__textarea::placeholder {
    font-size: 0.8em;
    color: #aaa;
}

.form__required {
    color: red;
    margin-left: 3px;
}

.form__error,
.form__error {
    color: red;
    font-size: 0.9em;
    height: 1.1em;
    margin: 0.3em auto;
    font-weight: 700;
    text-align: center;
}

.feedback-message {
    color: springgreen;
    font-size: 1em;
    font-weight: 700;
    width: 80%;
    text-align: center;
    margin: 0 auto 20px auto;
    height: 1.5em;
}

@media (max-width: 950px) { .form-container form { width: 60%; } }
@media (max-width: 750px) { .form-container form { width: 70%; } }
@media (max-width: 550px) { 
    .form-container form { width: 75%; } 
    .feedback-message { font-size: 0.9em; }
}

@media (max-width: 400px) { .form-container form { padding: 0.8em 1.25em 1.75em 1.25em; } }
@media (max-width: 350px) { .form-container form { padding: 0.8em 1em 1.75em 1em; } }

.navbar {
    background-color: #333;
    height: 9vh;
    width: 100%;
    z-index: 9;
    --nav-links-font-family: 'Montserrat', sans-serif;
    --nav-links-color: springgreen;
    --nav-links-hover-color: #ffff80;
    --nav-links-active-color: white;
}

.nav-links {
    font-size: 1.4rem;
    padding: 0;
    margin: 0 auto;
    height: 9vh;
    line-height: 9vh;
    text-align: center;
    list-style-type: none;
}

.nav-links li {
    display: inline-block;
    padding: 0 1.1em;
    height: 9vh;
    line-height: 9vh;
}

.nav-links__a {
    font-family: var(--nav-links-font-family);
    font-weight: 600;
    color: var(--nav-links-color);
    text-decoration: none;
}

.activeLink {
    color: var(--nav-links-active-color);
    border-bottom: 2px solid var(--nav-links-active-color);
}

.nav-links__a:hover {
    color: var(--nav-links-hover-color);
    border-bottom: 2px solid var(--nav-links-hover-color);
}


@media screen and (max-width: 1100px) {
    ul.nav-links li { font-size: 1.2rem; }
}

@media screen and (max-width: 800px) {
    ul.nav-links li { 
        font-size: 1.15rem;
        padding: 0 0.8em; 
    }
}

@media screen and (max-width: 700px) {
    ul.nav-links { 
        position: static; 
        width: 100%;
    }
    ul.nav-links li { font-size: 1.2rem; }
}

@media screen and (max-width: 525px) { ul.nav-links li { font-size: 1.1rem; } }

@media screen and (max-width: 450px) {
    ul.nav-links li { 
        font-size: 1.0rem;
        padding: 0 0.7em;  
    }
}

@media screen and (max-width: 400px) { ul.nav-links li { padding: 0 0.6em; } }

@media screen and (max-width: 360px) {
    ul.nav-links li { 
        font-size: 0.9rem;
        padding: 0 0.5em; 
    }
}




.app-section {
    width: 100%;
}

.app-cards-container {
    width: 90%;
    max-width: 1200px;
    margin: 50px auto 150px auto;
    padding: 0;
    display: grid;
    grid-template-columns:  1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
    grid-row-gap: 30px;
    row-gap: 30px;
    justify-items: center;   
}

.app-card {
    position: relative;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    height: 22.5em;
    width: 18.75em;
    background-color: #333;
    border-radius: 0.75em;
    border: 1px solid #181818; 
    margin: 0px;
    padding: 1em;
    transition: 0.2s;
}

.app-card:hover {
    background-color: #383838;
}

.app-card__title {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.15em;
    font-weight: 600;
    text-align: center;
    color: var(--pale-green-color);
    padding: 0.2em 0.5em;
    margin: 0.5em auto;
}

.app-card__description {
    font-family: 'Open Sans', sans-serif;
    font-size: 0.925em;
    color: var(--light-yellow-color);
    padding: 0.2em 1.25em;
    margin: 0px auto;
    line-height: 1.5;
    font-weight: 400;
}

.app-card__ul {
    font-family: 'Open Sans', sans-serif;
    font-size: 0.925em;
    margin: 0 auto;
    padding: 0 2.5em;
    position: absolute;
    top: 54%; 
    left: 1.25em; 
}

.app-card__li {
    color: rgb(0, 210, 255);    
    padding: 1px;
}

.app-card__buttons-container {
    position: relative;
    width: 90%;
    margin: 0 auto;
    text-align: center;
    position: absolute;
    bottom: 0px;
    left: 1.25em;
}

.app-card__button {
    font-family: 'Open Sans', sans-serif;
    background-color: mediumvioletred;
    cursor: pointer;
    margin: 0.25em 0.6em 1.25em 0.6em;
    border-radius: 0.5em;
    padding: 0.3em 1em;
    color: white;
    font-size: 0.875em;
    font-weight: 400;
    letter-spacing: 1px;
    text-decoration: none;
    text-transform: uppercase;
    outline: none;
    border: none;
    transition: 0.25s;
}

.app-card__button:hover {
    background-color: rgb(202, 70, 198);
}

.app-card__button:active {
    background-color: darkorchid;
}

.app-card__button:disabled {
    background-color: #666;
    color: white;
    cursor: default;
}


