.logos-container {
    width: 80%;
    max-width: 1060px;
    margin: 0 auto 150px auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
