    justify-items: center;
}

.page-intro-text {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 0px;
    padding: 0.25rem;
    margin: 2rem auto;
    text-align: center;
    color: var(--light-blue-color);
}

.logo-card {
    height: 160px;
    min-width: 200px;
    border-radius: 10px;
